
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from "@vuepic/vue-datepicker";
import "@vuepic/vue-datepicker/dist/main.css";
import { BANKReportDetail, NBCReportDetail, RPT205000811_BODY, RPT205000811_RES } from "@/shared/type/RPT205000811.type";
import { defineComponent } from "vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { ClientSidePagination } from "@/shared/services/ClientSidePagination";

const pagination = new ClientSidePagination()

export default defineComponent({
    name: 'rpt202001ss17',

    components: { Pagination, VueDatePicker },

    data() {
        return {
            PAGE_SIZE,
            reportList: {
                nBCMonthlyReport: [] as NBCReportDetail[],
                intraAndInterBankReport: [] as BANKReportDetail[]
            },
            headers: {
                nbc: [
                    { columnName: "Service Type" },
                    { columnName: "Number of Transaction in KHR" },
                    { columnName: "Number of Transaction in USD" },
                    { columnName: "Amount in KHR" },
                    { columnName: "Amount in USD" }
                ],
                bank: [
                    { columnName: "Service Type" },
                    { columnName: "Gap of Transaction Value" },
                    { columnName: "Number of Transaction" },
                    { columnName: "Number of Customer" },
                    { columnName: "Amount" }
                ]
            },
            paginations: {
                nbc: {
                    currentPage: 1,
                    isResetPage: false,
                },
            },
            date: '' as any
        }
    },

    computed: {
        nBCMonthlyReport(): NBCReportDetail[] {
            return pagination.paginate(this.paginations.nbc.currentPage, this.reportList.nBCMonthlyReport, PAGE_SIZE).itemsOnPage
        },
        bankReportKHR(): BANKReportDetail[] {
            return this.reportList.intraAndInterBankReport.filter((report: BANKReportDetail) => {
                return report.currencyCode == 'KHR'
            })
        },
        bankReportUSD(): BANKReportDetail[] {
            return this.reportList.intraAndInterBankReport.filter((report: BANKReportDetail) => {
                return report.currencyCode == 'USD'
            })
        },
        formattedDate(): string {
            return this.$datetime.formatVueMonthPicker(this.date)
        },
    },

    async created() {
        await this.getNBCReport()
    },

    methods: {
        async getNBCReport() {
            const reqBody: RPT205000811_BODY = {
                year: this.date == '' ? '' : `${this.date.year}`,
                month: this.date == '' ? '' : this.returnMonth(this.date.month)
            }
            const res = await this.$request.getRequest('RPT20500811', reqBody, false) as RPT205000811_RES
            if (res.header.result) {
                this.reportList = res.body
            }
        },

        handlePagination(pageNumber: number) {
            this.paginations.nbc.isResetPage = false;
            this.paginations.nbc.currentPage = pageNumber;
        },

        onRefresh() {
            this.date = ''
            this.getNBCReport();
        },

        returnMonth(month: number): string {
            if(month < 10){
                return `0${month + 1}`
            } else {
                return `${month + 1}`
            }
        }
    }
})
