export class ClientSidePagination {

    private calculateTotalPages(totalItems: number, itemsPerPage: number): number {
        return Math.ceil(totalItems / itemsPerPage);
    }

    private validateCurrentPage(currentPage: number, totalPages: number): number {
        return Math.max(1, Math.min(currentPage, totalPages));
    }

    paginate(currentPage: number, items: any[], itemsPerPage: number): PaginationResult {
        const totalItems = items.length;
        const totalPages = this.calculateTotalPages(totalItems, itemsPerPage);
        const validatedPage = this.validateCurrentPage(currentPage, totalPages);

        const startItem = (validatedPage - 1) * itemsPerPage;
        const endItem = Math.min(validatedPage * itemsPerPage, totalItems);

        const itemsOnPage = items.slice(startItem, endItem);

        return {
            currentPage: validatedPage,
            totalPages,
            startItem,
            endItem,
            itemsOnPage,
        };
    }
}

interface PaginationResult {
    currentPage: number;
    totalPages: number;
    startItem: number;
    endItem: number;
    itemsOnPage: any[];
  }